import {  useRef, useState } from "react"
import "./contact.scss"


import emailjs from '@emailjs/browser';
import {CloseRounded} from "@material-ui/icons"


const Contact = () => {
    
   const [success , setSuccess] = useState({
       name : "",
       state: false
   })
   const form= useRef()

   
   const submit = e=>{
    //    console.log("submit start");

    setSuccess(true)
        e.preventDefault()
             
        emailjs.sendForm('service_fw0cq4d', 'template_crgfc88', form.current , "mCeqyIFl62wQyde5w")
        .then((result) => {
            // console.log(result);
            if (result.text === "OK"){
                setSuccess({
                    name : data.user_name,
                    state : true
                })
                setData({
                    user_name : "",
                    user_email : "",
                    message:""
                })
            }
        }, (error) => {
            console.log(error);
        });
   }

   const [data , setData] = useState({
    user_name :"",
    user_email :"",
    message: ""
   })

   const hundelInput = (e, )=>{
        setData( prev=>({...prev ,[e.target.name] : e.target.value}))
    }
   

    if (success === true) {
        setTimeout(()=>{
            setSuccess({
                name : "",
                state: false
            });
        },5000)
    }
  

    return ( 
        <div className="contact" id="contact">
                
                <div className={success.state  ? "TaostMessage show" : "TaostMessage"}>
                    <p className="text">
                        thanks {success.name} I have receive your message and I will response as fast as posable
                    </p>

                    <p onClick={()=>setSuccess(false)} className="icon">
                        <CloseRounded size="sm" />
                    </p>                     
                </div>
                
            <h1>Contact Me</h1>
            <div className="ff">

            <form onSubmit={submit} ref={form}>
               <div className="inputGroup">
                   <label htmlFor="name">name :</label>
                   <input type="text" id="name"
                    name="user_name"
                    required
                    value={data.user_name}
                    onChange={hundelInput} />
               </div>
               <div className="inputGroup">
                   <label htmlFor="email">email :</label>
                   <input type="email" id="email"
                    name="user_email"
                    required
                    value={data.user_email}
                    onChange={hundelInput} />
               </div>
               <div className="inputGroup">
                   <label htmlFor="Message">Message :</label>
                   <textarea 
                    id="Message"
                    value={data.message}
                    name="message"
                    onChange={hundelInput} ></textarea>
               </div>

               <input type="submit" value="send" className="submit"/>
            </form>
            
            </div>
        </div>
     );
}
 
export default Contact;