
import "./portfolio.scss"
import works from  "../../data/works"

import {Autocomplete, Box, FormGroup, Stack, TextField} from "@mui/material"

import { register } from 'swiper/element/bundle';
import { useEffect, useState } from "react";
const Portfolio = () => {
    const optionExample = ["absolute beginner","beginner","medium" , "three"];
    const [level , setLevel] = useState("three");
    const handleLevel = (e , updatedValue)=>{
        setLevel(updatedValue)
    }
    const [dataDisplayed , setDisplayed] =useState(works)
    useEffect(()=>{
        if(level === null){
            setDisplayed(works)
        }
        else{
            setDisplayed(works.filter(
                data => data.level === level
            ))
        }

        // console.log({dataDisplayed} , "Test");
        
    },[level ])
    
  register()
    return ( 
        <div className="portfolio" id="portfolio">
            <Stack width="100%" sx={{
                display : "flex" ,
                alignItems:"center",
                justifyContent :"center",
                minHeight : "100px"
            }}>

                <Box width="500px">
                    <FormGroup>
                        <Autocomplete 
                        options={optionExample}
                        renderInput={param=><TextField variant="standard" {...param} label="level"/>}
                        selectOnFocus
                        value={level}
                        onChange={handleLevel}
                        />
                    </FormGroup>
                </Box>
                
            </Stack>
        

                <swiper-container 
                speed="1000"
                pagination="true" 
                pagination-clickable="true" 
                navigation="true"
                effect="coverflow" 
                grab-cursor="true" 
                centered-slides="true" 
                slides-per-view="auto" 
                coverflow-effect-rotate="100" 
                coverflow-effect-stretch="0"
                coverflow-effect-depth="10"
                coverflow-effect-modifier="1" 
                coverflow-effect-slide-shadows="true"
                pagination-dynamic-bullets="true"
                // autoplay-delay="5500" 
                // autoplay-disable-on-interaction="false"
                
                style={{
                    display :"flex",
                    alignItems :"center",
                    justifyContent :"center",
                    width :"100vw",
                    height:"70vh"
                }}
                >
        { dataDisplayed.map( (img,rowId)=>{
            return(
                <swiper-slide key={rowId}
                
                style={{
                    width :"50%",
                    height : "90vh",
                    display :"flex",
                    alignItems :"center",
                    justifyContent :"center",
                    backgroundImage:"linear-gradient(rgba(255, 255, 255 ,.9) , rgba(255, 255, 255 ,.6))",
                    fontSize :"20px"

                }} 
                className="portfolio-swiper"
                >
                    <div className="item">
                        <div className="left">
                                <div className="title">{img.title}</div>
                                <div className="desc">{img.desc}</div>
                                <div className="link">
                                    <a href={img.link} target="_blank" rel="noreferrer">view site</a>
                                </div>
                        </div>
                        <div className="right" style={{
                            minHeight : "150px"
                        }}>
                            { typeof(img.url)  !== "object" && 
                            <img src={img.url}
                            style={{"height":"100%" , "width":"100%"}}
                            alt=""/>
                            }
                            { typeof(img.url)  === "object" &&
                            <swiper-container 
                            // pagination="true" 
                            effect="cube" 
                            grab-cursor="true" 
                            cube-effect-shadow="true"
                            cube-effect-slide-shadows="false" 
                            cube-effect-shadow-offset="20" 
                            cube-effect-shadow-scale="0.94"
                            autoplay-delay="3000" 
                            autoplay-disable-on-interaction="false"
                            style={{
                                height:"90%",
                                width : "100%",
                                backgroundColor:"white"
                            }}
                            >
                           <swiper-slide style={{
                                backgroundPosition: "center",
                                backgroundSize: "cover",
                           }}
                           >
                               <img src={img.url[0]} alt="" style={{
                                   display:"block",
                                   width:"100%",
                                   height:"100%",
                               }}/>
                           </swiper-slide>
                           <swiper-slide style={{
                                backgroundPosition: "center",
                                backgroundSize: "cover"
                           }}>
                               <img src={img.url[1]} alt="" style={{
                                   display:"block",
                                   width:"100%",
                                   height:"100%"
                               }}/>
                           </swiper-slide>
                           <swiper-slide style={{
                                backgroundPosition: "center",
                                backgroundSize: "cover"
                           }}> 
                               <img src={img.url[2]} alt="" style={{
                                   display:"block",
                                   width:"100%",
                                   height:"100%"
                               }}/>
                           </swiper-slide>
                           <swiper-slide style={{
                                backgroundPosition: "center",
                                backgroundSize: "cover"
                           }}>
                               <img src={img.url[3]} alt="" style={{
                                   display:"block",
                                   width:"100%",
                                   height:"100%"
                               }}/>
                           </swiper-slide>
                          </swiper-container>
                            }
                        </div>
                    </div>
                </swiper-slide>
            )
        }
            )}
            </swiper-container>
        </div>
     );
}
 
export default Portfolio;